import { mapState } from 'vuex'
export default {
    computed: {
        ...mapState(['userInfo']),
    },
    data() {
        return {
            recordList: []
        }
    },
    created() {
        this.getUserRecord()
    },
    methods: {
        showDay (index) {
            if (index === 0) {
                return true
            }
            let temp1 = this.recordList[index]
            let temp2 = this.recordList[index - 1]
            let str1 = this.$timeText(temp1.createTime)
            let str2 = this.$timeText(temp2.createTime)
            let arr1 = str1.split(' ')
            let arr2 = str2.split(' ')
            return arr1[0] !== arr2[0]
        },
        dayText (txt) {
            let str = this.$timeText(txt)
            let arr = str.split(' ')
            return arr[0]
        },   
        timeText (txt) {
            let str = this.$timeText(txt)
            let arr = str.split(' ')
            return arr[2]
        },   
        async getUserRecord () {  
            let params = {
                pageNo: 1,
                pageSize: 100,
                // verifyUsername: this.userInfo.username
            }
            const res = await this.$api.getFootprint(params)
            if (res.code === '0') {
                this.recordList = res.data.list
            }
        },
    },
}
